#business {
  position: relative;
  height: 860px;
  margin-top: -100px;
  .shadow {
    z-index: 1;
    position: absolute;
    width: 154px;
    height: 179px;
    box-shadow: 0 0 60px rgba(0, 2, 6, 0.2); }
  .container {
    padding: 0;
    height: 100%;
    position: relative;
    z-index: 2;
    max-width: 1200px;
    .shadow.left {
      left: 0px;
      top: 0px; }
    .shadow.right {
      right: 0px;
      top: 0px; }
    .shadow.bot-left {
      left: 0px;
      bottom: 0px; }
    .shadow.bot-right {
      right: 0px;
      bottom: 0px; }
    .wrap {
      padding: 106px 92px;
      background: #fff;
      position: relative;
      z-index: 2;
      height: 100%;
      .columns {
        margin: 0;
        padding: 0; }
      .about {
        h2 {
          width: 228px;
          color: #3e3e3e;
          border-left: 2px solid #29a2ff;
          padding-left: 27px;
          font-weight: 900; }
        .description {
          color: #3e3e3e;
          font-size: 14px;
          font-weight: 400;
          line-height: 20px;
          margin-top: 27px;
          margin-bottom: 33px; } }
      .columns.specification {
          height: 250px;
          a {
            padding: 20px 20px;
            width: 100%;
            height: 100%;
            display: block;
            .title {
              margin-top: 30px;
              margin-bottom: 10px;
              color: #3e3e3e;
              font-family: gotham;
              font-size: 16px;
              font-weight: 700;
              text-transform: uppercase; }
            .description {
              font-size: 13px;
              color: #a7a7a7; } }
          &:hover {
            background-color: #003c84;
            background-image: radial-gradient(circle 318px at 50.02% -52.18%, rgba(11, 135, 255, 0.8) 0%, transparent 100%);
            //+transition()
            .title,.description {
              //color: $white
              color: #fff; } }
          &:nth-child(1) {
            a {
              &:before {
                display: block;
                content: "";
                background: url("../img/vector/ico-hands.svg");
                width: 103px;
                height: 58px;
                background-size: 103px 58px; }
              &:hover {
                &:before {
                  background: url("../img/vector/ico-hands-hover.svg");
                  background-size: 103px 58px; } } } }
          &:nth-child(2) {
            a {
              &:before {
                display: block;
                content: "";
                background: url("../img/vector/ico-monitor.svg");
                width: 77px;
                height: 58px;
                background-size: 77px 58px; }
              &:hover {
                &:before {
                  background: url("../img/vector/ico-monitor-hover.svg");
                  background-size: 77px 58px; } } } }
          &:nth-child(3) {
            a {
              &:before {
                display: block;
                content: "";
                background: url("../img/vector/ico-world.svg");
                width: 76px;
                height: 76px;
                background-size: 76px 76px; }
              &:hover {
                &:before {
                  background: url("../img/vector/ico-world-hover.svg");
                  background-size: 76px 76px; } } } }
          &:nth-child(4) {
            a {
              &:before {
                display: block;
                content: "";
                background: url("../img/vector/ico-sound.svg");
                width: 86px;
                height: 75px;
                background-size: 86px 75px; }
              &:hover {
                &:before {
                  background: url("../img/vector/ico-sound-hover.svg");
                  background-size: 86px 75px; } } } }
          &:nth-child(5) {
            a {
              &:before {
                display: block;
                content: "";
                background: url("../img/vector/ico-cup.svg");
                width: 70px;
                height: 64px;
                background-size: 70px 64px; }
              &:hover {
                &:before {
                  background: url("../img/vector/ico-cup-hover.svg");
                  background-size: 70px 64px; } } } }
          &:nth-child(6) {
            a {
              &:before {
                display: block;
                content: "";
                background: url("../img/vector/ico-book.svg");
                width: 48px;
                height: 74px;
                background-size: 48px 74px; }
              &:hover {
                &:before {
                  background: url("../img/vector/ico-book-hover.svg");
                  background-size: 48px 74px; } } } }
          &:nth-child(7) {
            a {
              &:before {
                display: block;
                content: "";
                background: url("../img/vector/ico-star.svg");
                width: 66px;
                height: 70px;
                background-size: 66px 70px; }
              &:hover {
                &:before {
                  background: url("../img/vector/ico-star-hover.svg");
                  background-size: 66px 70px; } } } } } } } }

